.our_team{
	background-image: linear-gradient(177deg, #001942 24%, #164980 62%, #040c33);
	background-repeat: no-repeat;
	background-size: cover;
	&__container{
		width: 83.20%;
		margin: 0 auto;
		display: flex;
		justify-content: space-between;
		flex-direction: column;
		@media screen and (max-width:480px) {
			width: 89.3%;
		}
		.title h2{
			font-family: 'Roboto', sans-serif;
			font-size: 41px;
			font-weight: normal;
			font-style: normal;
			font-stretch: normal;
			line-height: normal;
			letter-spacing: -0.5px;
			color: #f1a670;
			margin-bottom: 59px;
			margin-top: 50px;
			@media screen and (max-width:768px) {
				text-align: center;
			}
			@media screen and (max-width:480px) {
				font-size: 31px;
				letter-spacing: -0.4px;
				text-align: center;
				padding: 0px 30px 0 30px;
				margin-bottom: 25px;
			}
			@media screen and (max-width:380px) {
				padding: 0px 20px 0 20px;
			}
			@media screen and (max-width:380px) {
				padding: 0px 5px 0 5px;
			}
		}
		.team_cards{
			display: flex;
			flex-flow: row wrap;
			margin-bottom: 50px;
			@media screen and (max-width:1024px) {
				justify-content: space-around;
			}
			.card{
				padding: 34px 34px 28px 34px;
				margin-right: 25px;
				margin-bottom: 25px;
				background: #fff;
				display: flex;
    			flex-direction: column;
    			justify-content: center;
    			align-items: center;
				width: calc(24% - 5px);
    			&:nth-child(4n){
					margin-right: 0;
				}
				@media screen and (max-width:1651px) {
					width: calc(24% - 6px);
				}
				@media screen and (max-width:1535px) {
					width: calc(24% - 8px);
				}
    			// @media (max-width: 1200px) and (min-width: 768px){
    			// 	width: calc(30% - 19px);
    			// }
    			@media screen and (max-width:1440px) {
					width: calc(25% - 19px);
					&:nth-child(4n){
						margin-right: 0;
					}
				}
				@media screen and (max-width:1200px) {
					width: calc(33% - 14px);					
					&:nth-child(3n){
						margin-right: 0;
					}
					&:nth-child(4n){
						margin-right: 25px;
					}
				}
    			@media screen and (max-width:1024px) {
					width: 46%;
					margin-right: 0;
					&:nth-child(4n){
						margin-right: 0;
					}
				}
    			@media screen and (max-width:768px) {
					width: 46%;
					margin-right: 0;
				}
				@media screen and (max-width:650px) {
					width: 100%;
				}

				&:hover{
					background: linear-gradient(to left, #ef7d2d, #f9a86f);
					.card__name, .card__text, .position{
						color: #fff;
					}
					.card__photo{
						box-shadow: none;
						img{
							-webkit-filter: grayscale(0);
    						filter: grayscale(0);
						}
					}
				}
				&__photo{
					height: 127px;
    				width: 127px;
    				margin: 0 auto;
    				border-radius: 50%;
    				position: relative;
    				box-shadow: 0 7px 10px 0 #d8d8d8;
    				overflow: hidden;
    				img{
    					transition: all .25s ease-in-out;
    					-o-object-fit: cover;
    					object-fit: cover;
    					width: 100%;
    					height: 100%;
    					border-radius: 50%;
    					-webkit-filter: grayscale(.7);
    					filter: grayscale(.7);
    				}
				}
				&__name{
					font-family: 'Montserrat',sans-serif;
    				-webkit-font-smoothing: antialiased;
					font-size: 18px;
    				line-height: 23px;
    				letter-spacing: -.3px;
    				color: #32354a;
    				text-align: center;
    				margin-top: 15px;
				}
				.position{
					font-family: 'Montserrat',sans-serif;
					font-size: 14px;
    				font-weight: 600;
    				line-height: 23px;
    				letter-spacing: -.2px;
    				color: #f1a670;
    				margin-top: 5px;
    				text-align: center;
				}
				&__text{
					font-family: 'Montserrat',sans-serif;
    				-webkit-font-smoothing: antialiased;
    				-moz-osx-font-smoothing: grayscale;
    				font-size: 14px;
    				line-height: 1.5;
    				letter-spacing: -.2px;
    				color: #32354a;
    				margin-top: 15px;
    				padding: 0 7px;
    				text-align: center;
				}
			}
		}
	}
}